@import "../mixins.scss";

.your-name {
    background-color: #ffffff;
    color: #000000;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    @import "../variable.scss";

    @media (min-width:$tablet-width) {
        width: 280px;
        padding-right: 43px;
        border-right: 1px #DBDBDB solid;
    }

    @media (min-width:$desktop-width) {        
        width: 430px;
        padding-right: 63px;
        border-right: 1px #DBDBDB solid;
    }
}

.your-name__list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-content: center;
}

.your-name__item {
    margin-bottom: 16px;

    
}

.your-name__item:last-child {
    margin-bottom: 0;

    @media (min-width:$tablet-width) {
        margin-bottom: 16px;
    }
}

.your-name__title {
    @include title-style-black;
    margin: 0;
    margin-bottom: 20px;
    padding: 0;

    @media (min-width:$tablet-width) {
        font-size: 21px;
    }
}

.your-name__name {
    @include description-style-black;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    text-align: left;
}

.your-name__name small {
    margin-left: 16px;
}

.your-name__input-your-name {
    width: 280px;
    height: 58px;
    border: none;
    padding: 16px;
    margin: 0;
    box-sizing: border-box;
    @include title-style-black;
    background-color: #F2F2F2;

    @media (min-width:$desktop-width) {
        width: 430px;
    }

    &:hover {
        background-color: #E5E5E5;
    }

    &:active {
        background-color: #ffffff;
        border: 4px #E5E5E5 solid;

    }

    &:focus-visible{ 
        outline: none;
    }
}
